import { categoryData } from "@/data";
import { CategoryCard } from "../card";
import { Container } from "../container";
import { Title } from "../title";
import { ArrowRight } from "lucide-react";
import { SidebarTopIcon } from "@/assets/icons";

export const HomeCategory = () => {
  return (
    <section className="my-12">
      <Container>
        <Title className="mb-[28px]">Популярные категории</Title>
        <div className="grid grid-cols-6 gap-5 ">
          {categoryData.slice(0, 11).map((item, index) => (
            <CategoryCard key={index} category={item} />
          ))}
          <div className="bg-white relative shadow-2xl overflow-hidden pb-1 min-h-[190px] rounded-[10px] cursor-pointer flex flex-col justify-between">
            <div className="px-4 py-6">
              <ArrowRight />
              <SidebarTopIcon />
            </div>
            <div className="px-4">
              <p className="font-semibold text-sm text-textColor leading-[21px]">
                Смотреть весь каталог
              </p>
            </div>
            <div className="px-4">
              <p className="text-sm font-normal text-darkSoul">123123 товар</p>
            </div>
            <div className="bg-[#0054AE1F] w-[150px] h-[150px] rounded-full absolute top-[8px] -left-[82px]"></div>
            <div className="bg-[#0054AE1F] w-[270px] h-[270px] rounded-full absolute -top-[48px] -left-[122px]"></div>
          </div>
        </div>
      </Container>
    </section>
  );
};

export default HomeCategory;
